import React from 'react'
import { graphql } from 'gatsby'
import Layout from '../components/Layout'
import GyroController from '../components/GyroController'
import SmallHeader from '../components/SmallHeader'
import MainCreative from '../components/MainCreative'
import Footer from '../components/Footer'

class CreativePage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      timeout: false,
      loading: 'is-loading'
    }
    this.setWrapperRef = this.setWrapperRef.bind(this);
  }

  componentDidMount () {
    this.timeoutId = setTimeout(() => {
      let s=document.querySelector('#instructions').style;
      s.display='none';
      this.setState({
        loading: '',
      });
    }, 500);
  }

  componentWillUnmount () {
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
    }
  }

  setWrapperRef(node) {
    this.wrapperRef = node;
  }

  render() {
    return (
      <Layout location={this.props.location} position={this.state.position}>
        <div id="subwrapper" className="page">
        <SmallHeader timeout={this.state.timeout} active="creative" />
          <GyroController>
            <MainCreative data={this.props.data}
            timeout={this.state.timeout}
            setWrapperRef={this.setWrapperRef} />
            { Instructions() }
          </GyroController>
          <Footer  timeout={this.state.timeout} />
        </div>
        <div id="bg"></div>
      </Layout>
    )
  }
}

export default CreativePage

const Instructions = () => {
  return (
    <div className="content" id="instructions">
        <div className="inner">
          <h3>a device-only experience</h3>
          <p className="singleSpace">
            from the center, tilt up or down
         <br />
           in one of the four directions
          <br/>
            selecting elements at the end
           </p>
      </div>
    </div>
  );
}

export const fluidImage = graphql`
fragment fluidImage on File {
  childImageSharp {
    fluid(maxWidth: 1024) {
      ...GatsbyImageSharpFluid
    }
  }
}
`;


export const pageQuery = graphql`
query pageQuery{
  allFile{
    edges{
      node{
        id
        ...fluidImage
      }
    }
  }
}
`;
